import { Component, OnInit, Input } from "@angular/core";
import { FormGroup } from "@angular/forms";

import { FieldConfig, Field } from "../../../models";

@Component({
	selector: "ngs-form-control-email",
	template: `<div  [formGroup]="group">
<mat-form-field fxFlexFill>
    <input matInput [type]="config.inputType" [placeholder]="config.title" [formControlName]="config.name">
</mat-form-field>
</div>`,
	styles: [`:host{display:block}.mat-form-field{width:100%}`]
})
export class EmailComponent implements Field {
	config: FieldConfig;
	group: FormGroup;

	constructor() {}

	ngOnInit() {}
}
